import React from 'react'
import Layout from '../../../Layouts/Layout'
import style from './updatesv.module.css'
export default function UpdateSinhVien() {
    return (
        <Layout>
            <div className={style.update_container}>
                <input placeholder="ID"></input>
                <input placeholder="Tên sinh viên"></input>
                <input placeholder="Ngày sinh"></input>
                <input placeholder="Lớp"></input>
                <input placeholder="Cố Vấn"></input>
                <button className={style.button_3}>Submit</button>
            </div>
        </Layout>
    )
}
